/**
 * api接口的统一封装
 */
import request from './request.js';

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */
export function fetch(url, params = {}) {

  return new Promise((resolve, reject) => {

    request
      .get(url, {
        params: params,
      })
      .then(response => {

        console.log('响应数据', response);

        response && response.data && resolve(response.data);

      })
      .catch(err => {

        reject(err);

      });

  });

}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */
export function post(url, data = {}, config) {

  const cancelRequestType = data.cancelRequestType;
  if (data.cancelRequestType) {

    delete data.cancelRequestType;

  }
  return new Promise((resolve, reject) => {

    request
      .post(url, data, {params: {cancelRequestType},
        ...config})
      .then(response => {

        resolve(response.data);

      })
      .catch(err => {

        reject(err);

      });

  });

}

export function download(url, data = {}, config) {

  return new Promise((resolve, reject) => {

    request
      .post(url, data, config)
      .then(response => {

        resolve(response);

      })
      .catch(err => {

        reject(err);

      });

  });

}

