/** 格式化分页数据 */
export function formatPageResponse(response) {

  const newData = response;
  const result = response.result;
  if (result) {

    newData.result = result[0];
    newData.page = result[1];

  } else {

    newData.result = [];
    newData.page = {
      records: [],
      total: 0,
      size: 0,
      current: 1,
      orders: [],
      searchCount: true,
      pages: 0,
    };

  }
  return newData;

}
