import axios from 'axios';

const baseURL = '/apis/';

axios.defaults.headers['Content-Type'] = 'application/json';

const service = axios.create({
  baseURL,
  timeout: 30000,
});

service.interceptors.response.use(
  async function (response) {

    return Promise.resolve(response);

  },
  function (error) {

    if (error.response) {

      switch (error.response.status) {

        case 404:
          console.error('404,请求找不到！');
          break;
        case 500:
          console.error('500,服务器错误！');
          break;
        case 403:
          console.error('403,认证过期！');
          break;
        case 401:
          console.error('401,无权限！');
          break;

      }

    } else {

      console.error('未知错误，可能是断网了！');

    }
    return Promise.reject(error);

  },
);

service.interceptors.request.use(config => {
  let storeMainString = window.localStorage.getItem('store-main');
  console.log('storeMainString', storeMainString);
  if (storeMainString && typeof storeMainString === 'string') {
    let storeMain = JSON.parse(storeMainString);
    config.headers['Authorization'] = storeMain.token;
  }
  return config;

});
export default service;
