<template>
  <div id="float-ball" class="iframe-modal">
    <div class="popBox" id="iframe-modal-main" data-run="10">
      <div
        class="con flex1"
        :style="{ width: modalWidth, height: modalHeight }"
      >
        <div class="modal-header align-center">
          <div id="test" class="modal-header__title align-center">
            {{ modalTitle }}
          </div>
          <div
            v-show="modalTitle === '意见反馈'"
            @click="miniIframe"
            style="margin-right: 31px"
            class="modal-header__close cursor"
          >
            <img style="width: 20px; height: 10px" :src="hengang64" alt="" />
          </div>
          <div @click="closeIframe" class="modal-header__close cursor">
            <img
              style="width: 17px; height: 17px; top: 3px; position: relative"
              :src="close64"
              alt=""
            />
          </div>
        </div>

        <div
          id="iframe-modal-modal-content"
          class="iframe-modal-modal-content"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
import { message } from "ant-design-vue";
import { suspensionBall } from "./floating-sphere";
import {
  addBase64,
  listase64,
  enterBase64,
  hengang64,
  close64,
} from "./constant";
export default {
  name: "iframe-modal",
  components: {},
  data() {
    return {
      status: "",
      btnType: "", // iframeAddSrc或iframeListSrc
      addBase64: addBase64,
      listase64: listase64,
      enterBase64: enterBase64,
      hengang64: hengang64,
      close64: close64,
    };
  },
  props: {
    iframeParams: {
      type: Object,
      default: () => {},
    },
    activedIndex: {
      type: Number,
      default: -1,
    },
  },
  computed: {
    modalTitle() {
      return this.btnType === "iframeAddSrc"
        ? "意见反馈"
        : this.btnType === "iframeListSrc"
        ? "我的反馈"
        : "";
    },
    modalWidth() {
      return this.btnType === "iframeAddSrc" ? "800px" : "90vw";
    },
    modalHeight() {
      return this.btnType === "iframeAddSrc" ? "560px" : "80vh";
    },
  },
  methods: {
    queryIframe() {
      const whetherHaveIframe = document.getElementById("kanwuIframe");
      return whetherHaveIframe;
    },
    whetherHaveIframe() {
      const whetherHaveIframe = document.getElementById("kanwuIframe");
      return whetherHaveIframe ? true : false;
    },
    createIframe(iframeUrl) {
      let a = document.getElementById("iframe-modal-main");
      a.style.display = "flex";

      let b = document.getElementById("iframe-modal-modal-content");
      let iframe = document.createElement("iframe");

      // 设置iframe的样式
      iframe.id = "kanwuIframe";
      iframe.name = "kanwuIframe";

      iframe.style.width = "100%";

      iframe.style.height = "100%";

      iframe.style.margin = "0";

      iframe.style.padding = "0";

      iframe.style.overflow = "scroll";

      iframe.style.border = "none";

      iframe.src =
        this._props.iframeParams[iframeUrl] +
        `?token=${this._props.iframeParams.token}`;
      b.innerHTML = "";
      b.appendChild(iframe);
    },
    newIframe(iframeUrl) {
      this.btnType = iframeUrl;
      if (this.whetherHaveIframe()) {
        const queryIframe = this.queryIframe();
        queryIframe.src =
          this._props.iframeParams[iframeUrl] +
          `?token=${this._props.iframeParams.token}`;

        let a = document.getElementById("iframe-modal-main");
        a.style.display = "flex";

        if (this.status) {
          let a = document.getElementById("iframe-modal-main");
          a.style.opacity = 1;
          a.style.pointerEvents = "all";
        }
      } else if (this.status && this.modalTitle === "意见反馈") {
        let iframModalMain = document.getElementById("iframe-modal-main");
        iframModalMain.style.opacity = 1;
        iframModalMain.style.pointerEvents = "all";
        this.status = "";
      } else {
        this.createIframe(iframeUrl);
      }
    },
    miniIframe() {
      let iframDom = document.getElementById("kanwuIframe");
      let iframWindow = iframDom && iframDom.contentWindow;

      let a = document.getElementById("iframe-modal-main");
      a.style.opacity = 0;
      a.style.pointerEvents = "none";
      this.status = "最小化";
    },
    closeIframe() {
      let b = document.getElementById("iframe-modal-modal-content");
      b.innerHTML = "";

      let a = document.getElementById("iframe-modal-main");
      a.style.display = "none";
      this.status = "";
      this.$emit("onCloseIframe");
    },
    test() {
      console.log("点击test");
    },
  },
  mounted() {
    // suspensionBall("float-ball", "https://www.baidu.com");

    let _this = this;
    window.addEventListener(
      "message",
      function (e) {
        // eslint-disable-next-line no-debugger
        let iframDom = document.getElementById("kanwuIframe");
        let iframWindow = iframDom && iframDom.contentWindow;
        // 加一个模块。先是e.data不为空, 然后根据模块进行判断, 最后才是根据不同条件判断
        // 监听其他系统发来就绪消息, 收到后传送当前通信地址
        if (e.data && e.data.kwReadyBoolean) {
          iframWindow.postMessage(
            {
              iframeReady: true,
              message: "iframe握手请求",
              currentUrl: _this._props.iframeParams.currentUrl,
            },
            _this._props.iframeParams.iframeUrl
          );
          console.log("外部系统iframe握手请求");
        }
        if (e.data && e.data.submitBoolean && e.data.message === "关闭iframe") {
          console.log("提交成功, 外部系统已关闭iframe", e.data);
          _this.closeIframe();
        }
        // 关闭处理
        if (
          e.data &&
          e.data.closeIframeBoolean &&
          e.data.message === "关闭iframe"
        ) {
          console.log("点击取消, 外部系统关闭iframe", e.data);
          _this.closeIframe();
        }

        // token过期
        if (
          e.data &&
          e.data.reLoginBoolean &&
          e.data.message === "第三方系统token过期,需重新登录"
        ) {
          console.log("第三方系统token过期,需重新登录");
          _this.closeIframe();
          _this.$emit("onTokenExpiration");
        }
      },
      false
    );
  },
  watch: {
    activedIndex: {
      handler(val) {
        if (val != -1) {
          message.success(val);
          this.btnType = val % 2 === 1 ? "iframeAddSrc" : "iframeListSrc";
          this.createIframe(this.btnType);
        }
      },
      immediate: true,
    },
  },
};
</script>

<style scoped lang="less">
#float-ball {
  z-index: 99999 !important;
  text-align: center;
  line-height: 50px;
  border-radius: 50%;
  box-shadow: 5px 5px 40px rgba(0, 0, 0, 0.5);
  transition: all 0.08s;
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  top: 50%;
  left: 95vw;
  background: #fff;
}

.iframe-modal {
}
/*关闭弹窗*/
.popBox {
  display: none;

  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
}

/*打开弹窗*/
.popBox:target {
  align-items: center;
  display: flex;
  justify-content: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

/*设置弹窗内容*/
.popBox .con {
  background-color: #ffffff;
  border-radius: 2px;
  animation: MiniDialogWrapperShow 0.35s;
  animation-fill-mode: forwards;
  overflow: hidden;
}

/*关闭按钮*/
.popBox .close {
  display: block;
  position: relative;
}

/*弹窗遮罩层*/
.popBox::before {
  content: "";
  cursor: default;
  background-color: #00000073;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.modal-header {
  padding: 16px 24px;
  background: #fff;
  border-bottom: 1px solid #f0f0f0;
  border-radius: 2px 2px 0 0;
}
.modal-header .modal-header__title {
  margin: 0;
  color: #000000d9;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  word-wrap: break-word;
  flex: 1;
  display: flex;
  justify-content: center;
  font-weight: bold;
}
.modal-header .modal-header__close {
  color: #8c8c8c;
  position: absolute;
  right: 24px;
}
.modal-header .modal-header__close:hover {
  color: #000000bf;
}

.iframe-modal-modal-content {
  background: #ffffff;
  padding: 24px;
  overflow: hidden;
  flex: 1;
}

.iframe {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: scroll;
  border: none;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.align-center {
  display: flex;
  align-items: center;
}
.flex-bt {
  display: flex;
  justify-content: space-between;
}
.flex1 {
  display: flex;
  flex-direction: column;
}

.cursor {
  cursor: pointer;
}

.big-img {
}

#kanwuIframe #app {
  overflow: scroll !important;
}

#kanwuIframe > html {
  color: red !important;
}

@keyframes MiniDialogMaskShow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes MiniDialogWrapperShow {
  0% {
    opacity: 0;
    transform: scale(0.82);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

li {
  list-style: none;
}
.jdc-side {
}
.mod_hang_qrcode {
  position: relative;
}

.mod_hang_top {
  display: block;
  width: 60px;
  height: 60px;
  margin: 5px 0 0;
  text-align: center;
  // cursor: pointer;
  -webkit-transition: all 0.25s ease;
  -moz-transition: all 0.25s ease;
  transition: all 0.25s ease;
  background-color: transparent;
}

.mod_hang_top {
}
.mod_hang_appeal_btn {
  img {
    width: 50px;
    height: 50px;
    float: right;
  }

  .jdcfont {
    background: pink;
    border-radius: 50%;
  }
}

.mod_hang_appeal_show {
  .occupy-space {
    display: inline-block;
    width: 100%;
    height: 50px;
    pointer-events: none;
    background: transparent;
    &:hover {
      background: transparent !important;
    }
  }
  .bg-color {
    background: #0049b0;
  }
  ul {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
  }

  .btn-item {
    display: flex;
    align-items: center;
    padding: 0 6px;
    color: #fff;

    &:hover {
      background: #0989c590;
      color: #ffffff;
    }

    .jdcfont-img {
      width: 30px;
      height: 30px;
      margin-right: 10px;
    }
  }
}

.mod_hang_appeal:hover .mod_hang_appeal_show {
  display: block;
}

.mod_hang_appeal.show .mod_hang_appeal_show {
  display: block;
}
.mod_hang_appeal_show {
  display: none;
  position: absolute;
  bottom: -101px;
  right: 3px;
  width: 150px;
}
.mod_hang_appeal_show li {
}
.mod_hang_appeal_show li:hover {
  background: #f3f5fb;
}
.mod_hang_appeal_show li:after,
.mod_hang_appeal_show li:before {
  content: "";
  display: block;
  clear: both;
  height: 0;
  line-height: 0;
  visibility: hidden;
}
.mod_hang_appeal_show .icon_box,
.mod_hang_appeal_show .text_box {
  float: left;
  width: 215px;
  height: 90px;
}
.mod_hang_appeal_show .icon_box {
  width: 78px;
  line-height: 90px;
  text-align: center;
}
.mod_hang_appeal_show .icon_box i {
  color: #0989c5;
  font-size: 30px;
}
.mod_hang_appeal_show .text_box h5 {
  margin-top: 10px;
  font-size: 16px;
  color: #03004c;
  line-height: 24px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.mod_hang_appeal_show .text_box p {
  margin-top: 10px;
  max-height: 36px;
  overflow: hidden;
  line-height: 18px;
  font-size: 12px;
  color: #666;
}

@media (max-width: 768px) {
  .jdc-rwd .jdc-side {
    display: none !important;
  }
}
</style>